.TeamPage {
    
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-row-gap: 15px;
    grid-column-gap: 15px;
    
    .team-name-section {
        grid-column: span 3;
        justify-self: start;
        align-self: end;
        .team-name {
            font-size: 4rem;
            margin-bottom: 20px;
        }
    }
    
    .win-loss-section {
        text-align: center;
        justify-self: center;
        padding: 40px;
    }

    .match-detail-section {
        grid-column: span 4;
    }
    
    .more-link {
        align-self: center;
        justify-self: center;
        font-size: 1.5rem;
    }
}